import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'mousFilter'
})
export class MousFilterPipe implements PipeTransform {
  patientNm: any;
  private datePipe: DatePipe;
  transform(
    MOUsData: any,
    licenseNmOrNo: string,
    licenseStartEndDt: string,
    issuingAuthority: string,
    remindDays: string,
  ) {
    if (MOUsData && MOUsData.length) {
      return MOUsData.filter(license => {
        if (
          (licenseNmOrNo && !license.licenseNmOrNo) ||
          // (licenseRegisterNo && !license.licenseRegisterNo) ||
          ( licenseStartEndDt && !license.licenseStartEndDt) ||
          // ( licenseEndDt && !license.licenseEndDt) ||
          (remindDays && !license.remindDays)
        ) {
          return false;
        }
        if (license.licenseNmOrNo) {
          if (
            licenseNmOrNo &&
            license.licenseNmOrNo.toLowerCase().indexOf(licenseNmOrNo.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }
        // mobile no
        if (license.licenseStartEndDt) {
          if (
            licenseStartEndDt &&
            license.licenseStartEndDt.toLowerCase().indexOf(licenseStartEndDt.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }
          if (license.issuingAuthority) {
            if (
              issuingAuthority &&
              license.issuingAuthority.toLowerCase().indexOf(issuingAuthority.toLowerCase()) ===
                -1
            ) {
              return false;
            }
          }
        // part status
        if (license.remindDays) {

          if (
            remindDays &&
            license.remindDays &&
            license.remindDays.toString()
                .toLowerCase()
                .indexOf(remindDays.toLowerCase()) === -1
        ) {
            return false;
        }
        }
        return true;
      });
    } else {
        return MOUsData;
    }
  }
}
