import { Injectable, Inject } from '@angular/core';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';
import { ApiService } from '@core/services/api.service';

@Injectable({
  providedIn: 'root'
})
export class StatutoryApiService {
  readonly MODULE_NAME = 'STATUTORY';
  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) { }
  insertOrUpdateOrgLicense<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.STATUTORY.INSERT_OR_UPDATE_ORG_LICENSE,
      payload
    );
  }
  fetchLicenses<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.STATUTORY.FETCH_LICENCES,
      payload
    );
  }
  fetchOrgLicenses<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.STATUTORY.FETCH_ORG_LICENSES,
      payload
    );
  }
  deleteOrgLicense<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.STATUTORY.DELETE_ORG_LICENSES,
      payload
    );
  }
  fetchLicensesToBeExpiryForRemainder<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.STATUTORY.FETCH_LICENSES_TO_BE_EXPIRY_FOR_REMAINDER,
      payload
    );
  }
  insertOrUpdateMoUs<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.STATUTORY.INSERT_OR_UPDATE_MOUS,
      payload
    );
  }
}
