import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { AddLicencesComponent } from '../add-licences/add-licences.component';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { AddMousComponent } from '../add-mous/add-mous.component';
import { StatutoryService } from '../../statutory.service';
import { Subscription } from 'rxjs';
export interface DialogData {
  shedNm: string;
  rowNo: string;
  columnNo: string;
  tierLocationNm: string;
  shedId: any;
  rowId: any;
  storagColumns: any;
  columnId: any;
  storagShelfs: any;
  tierId: any;
}
@Component({
  selector: 'app-statutory-calender',
  templateUrl: './statutory-calender.component.html',
  styleUrls: ['./statutory-calender.component.scss']
})
export class StatutoryCalenderComponent implements OnInit, OnDestroy {
  heading = 'Statutory Calendar';
  private subscription: Subscription;
  licences = true;
  mous = false;
  shedNm: any;
  rowNo: any;
  columnNo: any;
  expiry = true;
  showData = true;
  constructor(
    private router: Router,
    private activatedroute: ActivatedRoute,
    private dialog: MatDialog,
    private statutoryDataService: StatutoryService

  ) {}

  ngOnInit() {
    this.subscription =  this.statutoryDataService.showStatutory.subscribe(res => {
      console.log('subscriberCalled');
      if (res && res === 'license') {
        this.heading = 'statutory calendar';
        this.showData = true;
        this.licences = true;
        this.mous = false;
      } else if (res === 'mous') {
        this.heading = 'statutory calendar';
        this.showData = true;
        this.mous = true;
        this.licences = false;
      }
    });
    if (this.licences) {
    this.statutoryDataService.passStatutoryValue('licences');
    this.router.navigate(['statutory/licences']);
    }
    if (this.mous) {
      this.statutoryDataService.passStatutoryValue('mous');
      this.router.navigate(['statutory/mous']);
      }
      this.subscription = this.statutoryDataService.hideLicence.subscribe(res => {
        if (res && res === 'licence') {
        this.showData = false;
        this.heading = 'Update Licenses';
        } else if (res === 'agreement') {
          this.showData = false;
          this.heading = 'Update Agreements';
        }
      });
  }
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
  showLicences(value) {
    this.licences = true;
    this.mous = false;
    this.expiry = false;
    this.statutoryDataService.passStatutoryValue(value);

  }
  showMous(value) {
    this.mous = true;
    this.licences = false;
    this.expiry = false;
    this.statutoryDataService.passStatutoryValue(value);

  }
  showExpiry(value) {
    this.licences = false;
    this.mous = false;
    this.expiry = true;
    this.statutoryDataService.passStatutoryValue(value);

  }
  openLicenceDialog(): void {
    this.heading = 'Add Licenses';
    this.showData = false;
    this.statutoryDataService.sendUpdateLicenseData(null);
    this.router.navigate(['statutory/add-license']);
  }
  openMousDialog(): void {
    this.heading = 'Add Agreements';
    this.showData = false;
    this.statutoryDataService.sendUpdateLicenseData(null);
    this.router.navigate(['statutory/add-mous']);
  }
}
