import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { LicencesComponent } from './containers/licences/licences.component';
import { MousComponent } from './containers/mous/mous.component';
import { StatutoryCalenderComponent } from './containers/statutory-calender/statutory-calender.component';
import { AddLicencesComponent } from './containers/add-licences/add-licences.component';
import { AddMousComponent } from './containers/add-mous/add-mous.component';
export const statutoryRoutings: Routes = [
  {
    path: '',
    component: StatutoryCalenderComponent,
    children: [
      {
        path: 'licences',
        component: LicencesComponent
      },
      {
        path: 'mous',
        component: MousComponent
      },
      {
        path: 'add-license',
        component: AddLicencesComponent
      },
      {
        path: 'add-mous',
        component: AddMousComponent
      }
    ]
  },

];
@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(statutoryRoutings)
  ]
})
export class StatutoryRoutingModule { }
