import { Injectable, Inject } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { APP_CONSTANTS } from '@app/app.constant';
import { AppConstant } from '@core/models/app-constant';

@Injectable({
  providedIn: 'root'
})
export class StatutoryService {
statutoryChekedSource = new BehaviorSubject<any>([]);
statutoryChecked = this.statutoryChekedSource.asObservable();

fetchLicenseDataSource = new BehaviorSubject<any>([]);
fetchLicenseData = this.fetchLicenseDataSource.asObservable();

updateLicenseDataSource = new BehaviorSubject<any>(null);
updateLicenseDAta = this.updateLicenseDataSource.asObservable();

showStatutorySource = new BehaviorSubject<any>(null);
showStatutory = this.showStatutorySource.asObservable();

hideLicenceSource = new BehaviorSubject<any>(null);
hideLicence = this.hideLicenceSource.asObservable();
  constructor(@Inject(APP_CONSTANTS) private appConstants: AppConstant) { }
  passStatutoryValue(data: any) {
    this.statutoryChekedSource.next(data);
  }
  getBucketsConfig() {
    return {
      accessKeyId: this.appConstants.buckets.providerBuckt.accessKeyId,
      secretAccessKey: this.appConstants.buckets.providerBuckt.secretAccessKey,
      region: this.appConstants.buckets.providerBuckt.region
    };
  }

  getBucketName() {
    return {
      bucketURL: this.appConstants.buckets.providerBuckt.BucketURL,
      url: this.appConstants.buckets.providerBuckt.url
    };
  }
  sendFetchLicense(data: any) {
   this.fetchLicenseDataSource.next(data);
  }
  sendUpdateLicenseData(data: any) {
    this.updateLicenseDataSource.next(data);
  }
  getStatutory() {
    return this.showStatutory;
  }
  sendStatutory(data: any) {
    this.showStatutorySource.next(data);
  }
  hideLicences(data: any) {
    this.hideLicenceSource.next(data);
  }
}
