@Component({
  selector: 'app-add-licences',
  templateUrl: './add-licences.component.html',
  styleUrls: ['./add-licences.component.scss']
})
export class AddLicencesComponent implements OnInit , OnDestroy {
  private subscription: Subscription;
public   addLicensesForm: FormGroup;
  private sessionData: UserSession;
  licenceList ;
  licence: any;
  @ViewChild('fileInput') fileInput: ElementRef;
  @ViewChild('formDir') myForm: NgForm;
  public bucket = new S3(this.statutoryServiec.getBucketsConfig());
  obj: any;
  documentNm: any;
  fileUploadSubscription: Subscription;
  fileList: any;
  public url;
  public fileUpload: any;
  documentUrl = [];
  closeMat = false;
  LicenseData: {};
  urlResult = [];
  startDate;
  oldDate;
  updateData;
  isUpdated = false;
  submitForm = false;
  constructor(
    userService: UserService,
    private utilService: UtilService,
    // public dialogRef: MatDialogRef<LicencesComponent>,
    // private dialog: MatDialog,
    private statutoryApiService: StatutoryApiService,
    private statutoryServiec: StatutoryService,
    private snackbar: MatSnackBar,
    private _fb: FormBuilder,
    private datePipe: DatePipe,
    private router: Router,
  // @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) {
    this.sessionData = userService.getUser();
    const start  = new Date();
    this.startDate = this.datePipe.transform(
      start,
      'yyy-MM-dd'
    );
    const old = new Date('1990-01-01');
    this.oldDate = old;
  }

  ngOnInit() {
    this.fetchLicences();
    this.initForm();
    this.subscription =  this.statutoryServiec.updateLicenseDAta.subscribe(res => {
      if (res) {
        this.updateData = res;

        console.log('receiving data', this.updateData );
        this.addLicensesForm.get('licenseNm').patchValue(this.updateData.licenseNm);
        this.addLicensesForm.get('issuingAuthority').patchValue(this.updateData.issuingAuthority);
        this.addLicensesForm.get('remarks').patchValue(this.updateData.remarks);
        const modifiedStartDate = this.datePipe.transform(
          this.updateData.licenseStartDt,
          'yyy-MM-dd'
        );
        this.addLicensesForm.get('licenseStartDt').patchValue(modifiedStartDate);
        const modifiedEndDate = this.datePipe.transform(
          this.updateData.licenseEndDt,
          'yyy-MM-dd'
        );
         this.addLicensesForm.get('mobileNo').patchValue(this.updateData.mobileNo);
        this.addLicensesForm.get('emailId').patchValue(this.updateData.emailId);
        this.addLicensesForm.get('licenseEndDt').patchValue(modifiedEndDate);
        this.addLicensesForm.get('licenseRegisterNo').patchValue(this.updateData.licenseRegisterNo);
        this.documentUrl = this.updateData.documentUrl;
        this.addLicensesForm.get('remindDays').patchValue(this.updateData.remindDays);
        this.addLicensesForm.get('websiteAddress').patchValue(this.updateData.websiteAddress);
        this.addLicensesForm.get('addressLine1').patchValue(this.updateData.addressLine1);
        this.addLicensesForm.get('countryNm').patchValue(this.updateData.countryNm);
        this.addLicensesForm.get('stateNm').patchValue(this.updateData.stateNm);
        this.addLicensesForm.get('cityNm').patchValue(this.updateData.cityNm);
        this.addLicensesForm.get('zipcode').patchValue(this.updateData.zipcode);
      }
    });
    this.addLicensesForm.get('documentUrl').valueChanges.subscribe(res => {
      if (res) {
      const file = this.fileInput.nativeElement.files[0].name;
      this.urlResult.push(file);
      }
    });
    if (this.updateData) {
      this.addLicensesForm.get('licenseNm').valueChanges.subscribe(res => {
      this.isUpdated = res;
      });
    }
  }
  ngOnDestroy() {
  this.subscription.unsubscribe();
  }
  initForm() {
this.addLicensesForm = this._fb.group({
  licenseNm: [null, Validators.required],
  issuingAuthority: [null, Validators.required],
  remarks: '',
  documentUrl: '',
  mobileNo: [null ,   [
    Validators.pattern(/(5|6|7|8|9)\d{9}$/),
    Validators.minLength(10),
    Validators.maxLength(10),
    Validators.required
  ]],
  emailId: [null ,   [
    Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]$'),
    Validators.required
  ]],
  licenseStartDt: [null, Validators.required],
  licenseEndDt: [null, Validators.required],
  remindDays: [null, Validators.required],
  licenseRegisterNo: [null, Validators.required],
  websiteAddress: '',
  addressLine1 : '',
  countryNm : [null, Validators.required],
  stateNm : [null, Validators.required],
  cityNm : [null, Validators.required],
  zipcode : [null, Validators.required],
  addressLine2: ''
  // userNm : '',
  // usermobileNo : [null ,   [
  //   Validators.pattern(/(5|6|7|8|9)\d{9}$/),
  //   Validators.minLength(10),
  //   Validators.maxLength(10),
  // ]],
  // useremailId : [null ,   [
  //   Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$'),
  // ]],
});
  }
  // addUserDetails() {
  //   const email = this.addLicensesForm.get('useremailId').value;
  //   const mobile = this.addLicensesForm.get('usermobileNo').value;
  //   const name = this.addLicensesForm.get('userNm').value;
  //   if (email && mobile && name) {
  //   this.userDetails.push({
  //     userEmailId : email,
  //     userMobileNo: mobile,
  //      userNm: name
  //   });
  //   this.addLicensesForm.get('useremailId').patchValue('');
  //   this.addLicensesForm.get('usermobileNo').patchValue('');
  //   this.addLicensesForm.get('userNm').patchValue('');
  //   }
  // }
  // deleteLicense(index, user) {
  //   const payload =  {
  //       orgId: this.sessionData.orgId,
  //       rgGrpId: this.sessionData.orgGroupId,
  //       createdUsrId: this.sessionData.regId,
  //       userDelete: true,
  //       userLicenseId: user.userLicenseId
  //       // licenseInfoId: data.licenseInfoId
  //     };
  //     this.statutoryApiService.deleteOrgLicense(payload).subscribe(response => {
  //       if (response.responseCode === 'E200') {
  //         this.userDetails.splice(index , 1);
  //         this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
  //       } else if (response.responseCode === 'E400') {
  //         this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
  //       }
  //     });
  // }
  onNoClick(): void {
    this.addLicensesForm.reset();
    this.documentUrl = [];
    this.urlResult = [];
    this.updateData = null;
    this.statutoryServiec.sendStatutory('license');
    this.router.navigate(['statutory/licences']);
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  changeDateFunction(viewDate, date) {
    const modifiedDate = this.datePipe.transform(
      viewDate.value,
      'yyy-MM-dd'
    );
    if (date === 'startDate') {
    this.addLicensesForm.get('licenseStartDt').patchValue(modifiedDate);
    } else if (date === 'endDate') {
    this.addLicensesForm.get('licenseEndDt').patchValue(modifiedDate);
    }
  }
  fetchLicences() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      licenseType: 'License'
    };
    this.statutoryApiService.fetchLicenses(payload).subscribe(response => {
      if (response.responseCode === 'E200') {
        this.licenceList = response.data;
      } else if (response.responseCode === 'E400') {
        this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
      }
    });
  }
  listOfLicences(event) {
    this.licence = event ;
    console.log(this.licence, 'sel license');
      }
  insertLicences (data) {
    if (this.addLicensesForm.valid) {
      this.submitForm = false;
      const {addressLine2 , ...formValue} = this.addLicensesForm.value;

      const  payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        // moduleNm: null,
        licenseId:  this.licence.licenseId,
        documentUrl: this.documentUrl,
        licenseNm:   this.licence.licenseNm ,
        licenseType: 'License',
        // "toBeExpiryDays": null
      };
      const Payload = Object.assign(formValue, payload);
      const payloadArr = [];
      payloadArr.push(Payload);
      console.log(payload, 'payload');
      console.log(payloadArr, 'payload arrauy');
      this.statutoryApiService.insertOrUpdateOrgLicense(payloadArr).subscribe(response => {
        if (response.responseCode === 'E200') {
          this.addLicensesForm.reset();
          this.fetchLicenses();
          this.documentUrl = [];
          this.urlResult = [];
          this.updateData = null;
          this.subscription.unsubscribe();
          if (data === 'save') {
          this.statutoryServiec.sendStatutory('license');
          this.router.navigate(['statutory/licences']);
           }
        } else if (response.responseCode === 'E400') {
          this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackbar.open(errorResponse.error.validations[0].message, 'undo', {duration: 1500});
        } else {
          this.snackbar.open('Network is slow', 'undo', {duration: 1500});
        }
      }
      );
    } else {
      this.submitForm = true;
    }
  // }
  }
  updateLicences () {
    // if (this.addLicensesForm.valid) {
    const {addressLine2 , ...formValue} = this.addLicensesForm.value;

    const  payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      // moduleNm: null,
      licenseId:  this.updateData.licenseId,
      documentUrl: this.documentUrl,
      licenseNm:   this.updateData.licenseNm ,
      licenseInfoId: this.updateData.licenseInfoId,
      licenseType: 'License',
      isMouNameModified : true
      // "toBeExpiryDays": null
    };
    const Payload = Object.assign(formValue, payload);
    const payloadArr = [];
    payloadArr.push(Payload);
    console.log(payload, 'payload');
    console.log(payloadArr, 'payload arrauy');
    this.statutoryApiService.insertOrUpdateOrgLicense(payloadArr).subscribe(response => {
      if (response.responseCode === 'E200') {
        this.addLicensesForm.reset();
        this.fetchLicenses();
        this.documentUrl = [];
        this.urlResult = [];
        this.updateData = null;
        this.subscription.unsubscribe();
        this.submitForm = false;
        this.statutoryServiec.sendStatutory('license');
        this.router.navigate(['statutory/licences']);
      } else if (response.responseCode === 'E400') {
        this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
      }
    },
    (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackbar.open(errorResponse.error.validations[0].message, 'undo', {duration: 1500});
      } else {
        this.snackbar.open('Network is slow', 'undo', {duration: 1500});
      }
    }
    );
  // }
  }
  clearForm() {
    this.submitForm = false;
    console.log(this.myForm, 'form cjecking');
    if (!this.updateData) {
      this.documentUrl = [];
      this.urlResult = [];
    }
    this.addLicensesForm.reset();

  }
    // method for uploding file
    uploadfile(fileData) {
        this.fileList = fileData;
        const bucketDetails = this.statutoryServiec.getBucketName();
        const key =
          this.utilService.generateUniqueId() + '-' + this.fileUpload.name;
        const params = {
          Bucket: bucketDetails.bucketURL,
          Key: key,
          Body: this.fileList
        };
        this.obj = {
          documentNm: this.fileUpload[0].name,
         documentUrl: bucketDetails.url + key,
        };
        const awsfileUpload = this.bucket.putObject(params).promise();
        this.fileUploadSubscription = from(awsfileUpload).subscribe(() => {
          const url = bucketDetails.url + key;
          this.documentUrl.push(url);
          // if (!this.documentUrl) {
          //   this.documentUrl = url;
          // } else {
          //   this.documentUrl += ',' + url;
          // }
          console.log(this.documentUrl, 'file upload url Or Name');
        });
    }
    // file upload
    onFileChanged($event) {
      const file = this.fileInput.nativeElement.files[0];
      this.fileUpload = this.fileInput.nativeElement.files;
      this.documentNm = this.fileInput.nativeElement.files[0].name;
      if (this.fileUpload.length > 0) {
      for (let i = 0; i < this.fileUpload.length; i++) {
        const reader = new FileReader();
        reader.readAsDataURL(this.fileUpload[i]);
        reader.onload = () => {
          this.url = reader.result;
        };
        this.uploadfile(this.fileUpload[i]);
    }
      }
    }
    removeFile(i) {
      this.urlResult.splice(i, 1);
      this.documentUrl.splice(i, 1);
    }
    // method for fetchimg orglicences for license component
    fetchLicenses () {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        licenseType: 'License',
        isRenewal: 'false'
      };
      this.statutoryApiService.fetchOrgLicenses(payload).subscribe(response => {
        if (response.responseCode === 'E200') {
          const LicenseData = response.data;
          this.statutoryServiec.sendFetchLicense(LicenseData);
        } else if (response.responseCode === 'E400') {
          this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
        }
      });
    }
}
