import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'licenseFilter'
})
export class LicenseFilterPipe implements PipeTransform {
    patientNm: any;
    private datePipe: DatePipe;
    transform(
      licenseData: any,
      licenseNmOrNo: string,
      licenseStartEndDt: string,
      issuingAuthority: string,
      remindDays: string,
      emailId: string,
      mobileNo: string
    ) {
      if (licenseData && licenseData.length) {
        return licenseData.filter(license => {
          if (
            (licenseNmOrNo && !license.licenseNmOrNo) ||
            ( licenseStartEndDt && !license.licenseStartEndDt) ||
            (remindDays && !license.remindDays) ||
            ( issuingAuthority && !license.issuingAuthority)
          ) {
            return false;
          }
          if (license.licenseNmOrNo) {
            if (
              licenseNmOrNo &&
              license.licenseNmOrNo.toLowerCase().indexOf(licenseNmOrNo.toLowerCase()) ===
                -1
            ) {
              return false;
            }
          }
          // mobile no
          if (license.licenseStartEndDt) {
            if (
              licenseStartEndDt &&
              license.licenseStartEndDt.toLowerCase().indexOf(licenseStartEndDt.toLowerCase()) ===
                -1
            ) {
              return false;
            }
          }
            if (license.issuingAuthority) {
              if (
                issuingAuthority &&
                license.issuingAuthority.toLowerCase().indexOf(issuingAuthority.toLowerCase()) ===
                  -1
              ) {
                return false;
              }
            }
          // part status
          if (license.remindDays) {

            if (
              remindDays &&
              license.remindDays &&
              license.remindDays.toString()
                  .toLowerCase()
                  .indexOf(remindDays.toLowerCase()) === -1
          ) {
              return false;
          }
          }

  if (license.emailId) {
    if (
      emailId &&
      license.emailId.toLowerCase().indexOf(emailId.toLowerCase()) ===
        -1
    ) {
      return false;
    }
  }
  if (license.mobileNo) {
    if (
      mobileNo &&
      license.mobileNo.toLowerCase().indexOf(mobileNo.toLowerCase()) ===
        -1
    ) {
      return false;
    }
  }
          return true;
        });
      } else {
          return licenseData;
      }
    }
  }
