import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
export interface DialogData {
  zipCode: string;
  country: string;
  state: string;
  city: string;
  contactNo: string;
  website: string;
  addressLine1: string;
  addressLine2: string;
  emailId: any;
  mobileNo: any;
  websiteAddress: string;
}
@Component({
  selector: 'app-view-full-address',
  templateUrl: './view-full-address.component.html',
  styleUrls: ['./view-full-address.component.scss']
})
export class ViewFullAddressComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<ViewFullAddressComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {
    console.log(data.emailId, 'view more addrws');
  }

  ngOnInit() {
  }
  closeDialog() {
   this.dialogRef.close();
  }
}
