import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef, ChangeDetectorRef, Inject } from '@angular/core';
// import { MatSidenav, MatSnackBar, MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '../../../../../../node_modules/@angular/material';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { MousComponent } from '../mous/mous.component';
import { DialogData } from '../statutory-calender/statutory-calender.component';
import {
  //  MatDialogRef, MatDialog, MAT_DIALOG_DATA,
  MatSnackBar, MatDatepicker } from '@angular/material';
import { StatutoryApiService } from '@core/services/statutory-api.service';
import * as S3 from 'aws-sdk/clients/s3';
import { StatutoryService } from '../../statutory.service';
import {UtilService} from '@core/services/utils.service';
import { Subscription, from } from 'rxjs';
import {FormGroup, FormBuilder, Validators, NgForm} from '@angular/forms';
import { DatePipe } from '@angular/common';
import { HttpErrorResponse } from '@angular/common/http';
import {Router} from '@angular/router';
@Component({
  selector: 'app-add-mous',
  templateUrl: './add-mous.component.html',
  styleUrls: ['./add-mous.component.scss']
})
export class AddMousComponent implements OnInit {
  public mousForm: FormGroup;
  private sessionData: UserSession;
  filePath: any;
  @ViewChild('fileInput') fileInput: ElementRef;
  @ViewChild('formDir') myForm: NgForm;
  public bucket = new S3(this.statutoryService.getBucketsConfig());
  obj: any;
  documentNm: any;
  fileUploadSubscription: Subscription;
  fileList: any;
  public url;
  public fileUpload: any;
  documentUrl = [];
  licence: any;
  urlResult = [];
  closeMat = false;
  startDate;
  oldDate;
  licenseInfoId: any;
  updateData;
  isUpdated = false;
  submitForm = false;
  // userDetails = [];
  constructor(
    userService: UserService,
    // public dialogRef: MatDialogRef<MousComponent>,
    // private dialog: MatDialog,
    private snackbar: MatSnackBar,
    private statutoryService: StatutoryService,
    private utilService: UtilService,
    private statutoryApiService: StatutoryApiService,
    private _fb: FormBuilder,
    private router: Router,
    private datePipe: DatePipe,
  // @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) {
    this.sessionData = userService.getUser();
    const start  = new Date();
    this.startDate = this.datePipe.transform(
      start,
       'yyy-MM-dd'
    );
    const old = new Date('1990-01-01');
    this.oldDate = old;
  }

  ngOnInit() {
    this.initForm();
    this.statutoryService.updateLicenseDAta.subscribe(res => {
      if (res) {
        this.updateData = res;
        console.log('receiving data', res);
        this.mousForm.get('licenseNm').patchValue(this.updateData.licenseNm);
        this.mousForm.get('issuingAuthority').patchValue(this.updateData.issuingAuthority);
        this.mousForm.get('remarks').patchValue(this.updateData.remarks);
        const modifiedStartDate = this.datePipe.transform(
          this.updateData.licenseStartDt,
          'yyy-MM-dd'
        );
        this.mousForm.get('licenseStartDt').patchValue(modifiedStartDate);
        const modifiedEndDate = this.datePipe.transform(
          this.updateData.licenseEndDt,
          'yyy-MM-dd'
        );
         this.mousForm.get('mobileNo').patchValue(this.updateData.mobileNo);
        this.mousForm.get('emailId').patchValue(this.updateData.emailId);
        this.mousForm.get('licenseEndDt').patchValue(modifiedEndDate);
        this.mousForm.get('licenseRegisterNo').patchValue(this.updateData.licenseRegisterNo);
        this.documentUrl = this.updateData.documentUrl;
        this.mousForm.get('remindDays').patchValue(this.updateData.remindDays);
        this.mousForm.get('websiteAddress').patchValue(this.updateData.websiteAddress);
        this.mousForm.get('addressLine1').patchValue(this.updateData.addressLine1);
        this.mousForm.get('countryNm').patchValue(this.updateData.countryNm);
        this.mousForm.get('stateNm').patchValue(this.updateData.stateNm);
        this.mousForm.get('cityNm').patchValue(this.updateData.cityNm);
        this.mousForm.get('zipcode').patchValue(this.updateData.zipcode);
        // this.mousForm.get('contactPersonNm').patchValue(this.updateData.contactPersonNm);
      }
    });
    this.mousForm.get('documentUrl').valueChanges.subscribe(res => {
      let file = this.fileInput.nativeElement.files[0];
      if (file) {
        file = file.name || null;
        this.urlResult.push(file);
      }
    });
    if (this.updateData) {
      this.mousForm.get('licenseNm').valueChanges.subscribe(res => {
      this.isUpdated = res;
      });
    }
  }
  initForm() {
    this.mousForm = this._fb.group({
      licenseNm: [null, Validators.required],
      issuingAuthority: [null, Validators.required],
      remarks: '',
      documentUrl: '',
      mobileNo: [null ,   [
        Validators.pattern(/(5|6|7|8|9)\d{9}$/),
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.required
      ]],
      emailId: [null ,   [
        Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]$'),
        Validators.required
      ]],
      licenseStartDt: [null, Validators.required],
      licenseEndDt: [null, Validators.required],
      remindDays: [null, Validators.required],
      licenseRegisterNo: [null, Validators.required],
      websiteAddress: '',
      addressLine1 : '',
      countryNm : [null, Validators.required],
      stateNm : [null, Validators.required],
      cityNm : [null, Validators.required],
      zipcode : [null, Validators.required],
      userNm : '',
      addressLine2: '',
      usermobileNo : [null ,   [
        Validators.pattern(/(5|6|7|8|9)\d{9}$/),
        Validators.minLength(10),
        Validators.maxLength(10),
      ]],
      useremailId : [null ,   [
        Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$'),
      ]],
    });
  }

  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  changeDateFunction(viewDate, date) {
    const modifiedDate = this.datePipe.transform(
      viewDate.value,
       'yyy-MM-dd'
    );
    console.log(modifiedDate, 'view date');
    if (date === 'startDate') {
    this.mousForm.get('licenseStartDt').patchValue(modifiedDate);
    } else if (date === 'endDate') {
    this.mousForm.get('licenseEndDt').patchValue(modifiedDate);
    }
  }
  fetchOrgMous () {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      licenseType: 'MOU',
      isRenewal: 'false'
    };
    this.statutoryApiService.fetchOrgLicenses(payload).subscribe(response => {
      if (response.responseCode === 'E200') {
        const MOUsData = response.data;
        this.statutoryService.sendFetchLicense(MOUsData);
      } else if (response.responseCode === 'E400') {
        this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
      }
    });
  }
  onNoClick(): void {
    this.closeMat = true;
    this.mousForm.reset();
    this.documentUrl = [];
    this.urlResult = [];
    // this.dialogRef.close();
    this.statutoryService.sendStatutory('mous');
    this.router.navigate(['statutory/mous']);
    this.updateData = null;
  }
  listOfMous(event) {
    this.licence = event ;
    console.log(this.licence, 'sel license');
  }
  insertMous (data) {
    if (this.mousForm.valid) {
      this.submitForm = false;
      const {addressLine2 , ...formValue} = this.mousForm.value;
      const  payload = {
         orgId: this.sessionData.orgId,
         orgGrpId: this.sessionData.orgGroupId,
         createdUsrId: this.sessionData.regId,
         documentUrl: this.documentUrl,
        //  licenseNm:   this.licence.licenseNm ,
         licenseType: 'MOU',
       };
       const Payload = Object.assign(formValue, payload);
        this.statutoryApiService.insertOrUpdateMoUs(Payload).subscribe(response => {
          if (response.responseCode === 'E200') {
            this.mousForm.reset();
            this.documentUrl = [];
            this.urlResult = [];
            this.fetchOrgMous();
            this.updateData = null;
            if (data === 'save') {
              this.statutoryService.sendStatutory('mous');
              this.router.navigate(['statutory/mous']);
            }
          } else if (response.responseCode === 'E400') {
            this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackbar.open(errorResponse.error.validations[0].message, 'undo', {duration: 1500});
          } else {
            this.snackbar.open('Network is slow', 'undo', {duration: 1500});
          }
        }
          );
    } else {
      this.submitForm = true;
    }

  }
  updateMous () {
const {addressLine2 , ...formValue} = this.mousForm.value;
const  payload = {
   orgId: this.sessionData.orgId,
   orgGrpId: this.sessionData.orgGroupId,
   createdUsrId: this.sessionData.regId,
   documentUrl: this.documentUrl,
   licenseId:  this.updateData.licenseId,
   licenseNm:   this.updateData.licenseNm ,
   licenseInfoId: this.updateData.licenseInfoId,
   licenseType: 'MOU',
   isMouNameModified : true
 };
 const Payload = Object.assign(formValue, payload);
  this.statutoryApiService.insertOrUpdateMoUs(Payload).subscribe(response => {
    if (response.responseCode === 'E200') {
      this.mousForm.reset();
      this.documentUrl = [];
      this.urlResult = [];
      this.fetchOrgMous();
      this.updateData = null;
      this.submitForm = false;
      this.statutoryService.sendStatutory('mous');
      this.router.navigate(['statutory/mous']);
    } else if (response.responseCode === 'E400') {
      this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
    }
  },
  (errorResponse: HttpErrorResponse) => {
    if (errorResponse.status > 0) {
      this.snackbar.open(errorResponse.error.validations[0].message, 'undo', {duration: 1500});
    } else {
      this.snackbar.open('Network is slow', 'undo', {duration: 1500});
    }
  }
    );
}
clearForm() {
  this.submitForm = false;
  this.myForm.resetForm();
  console.log(this.myForm, 'checking form');
  if (!this.updateData) {
    this.documentUrl = [];
    this.urlResult = [];
  }
}
     // method for uploding file
     uploadfile(fileData) {
      this.fileList = fileData;
      const bucketDetails = this.statutoryService.getBucketName();
      const key =
        this.utilService.generateUniqueId() + '-' + this.fileUpload.name;
      const params = {
        Bucket: bucketDetails.bucketURL,
        Key: key,
        Body: this.fileList
      };
      this.obj = {
        documentNm: this.fileUpload[0].name,
       documentUrl: bucketDetails.url + key,
      };
      const awsfileUpload = this.bucket.putObject(params).promise();
      this.fileUploadSubscription = from(awsfileUpload).subscribe(() => {
        const url = bucketDetails.url + key;
        this.documentUrl.push(url);
        // if (!this.documentUrl) {
        //   this.documentUrl = url;
        // } else {
        //   this.documentUrl += ',' + url;
        // }
        console.log(this.documentUrl, 'file upload url Or Name');
      });
  }
  // file upload
  onFileChanged($event) {
    const file = this.fileInput.nativeElement.files[0];
    this.fileUpload = this.fileInput.nativeElement.files;
    this.documentNm = this.fileInput.nativeElement.files[0].name;
    if (this.fileUpload.length > 0) {
    for (let i = 0; i < this.fileUpload.length; i++) {
      const reader = new FileReader();
      reader.readAsDataURL(this.fileUpload[i]);
      reader.onload = () => {
        this.url = reader.result;
      };
      this.uploadfile(this.fileUpload[i]);
  }
    }
  }
  removeFile(i) {
    this.urlResult.splice(i, 1);
    this.documentUrl.splice(i, 1);
  }
}
