import { Component, OnInit } from '@angular/core';
import { StatutoryService } from '../../statutory.service';
import {  MatSnackBar, MatDialog } from '@angular/material';
import { StatutoryApiService } from '@core/services/statutory-api.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { AddMousComponent } from '../add-mous/add-mous.component';
import { DatePipe } from '@angular/common';
import { ViewFullAddressComponent } from '../view-full-address/view-full-address.component';
import { UsersDetailsComponent } from '../users-details/users-details.component';
import { Router } from '@angular/router';
@Component({
  selector: 'app-mous',
  templateUrl: './mous.component.html',
  styleUrls: ['./mous.component.scss']
})
export class MousComponent implements OnInit {
    licenseNmOrNo: string;
    licenseStartEndDt: string;
    issuingAuthority: string;
    remindDays: string;
  showMous: boolean;
  private sessionData: UserSession;
  MOUsData = [];
  constructor(
    private statutoryDataService: StatutoryService,
    userService: UserService,
    private dialog: MatDialog,
    private snackbar: MatSnackBar,
    private datePipe: DatePipe,
    private statutoryApiService: StatutoryApiService,
    private router: Router,
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.statutoryDataService.statutoryChecked.subscribe(res => {
      if (res && res === 'mous') {
        this.showMous = true;
      }
    });
    this. fetchMous ();
    this.statutoryDataService.fetchLicenseData.subscribe(res => {
      if (res) {
        this.MOUsData = res;
      }
    });
  }
  openMousDialog(data) {
    this.statutoryDataService.hideLicences('agreement');
    this.statutoryDataService.sendUpdateLicenseData(data);
    this.router.navigate(['statutory/add-mous']);
  }
  fetchMous () {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      licenseType: 'MOU',
      // isRenewal: 'false'
    };
    this.statutoryApiService.fetchOrgLicenses(payload).subscribe(response => {
      if (response.responseCode === 'E200') {
       const data = [];
       data.push(response.data);
       this.MOUsData = data[0];
      } else if (response.responseCode === 'E400') {
        this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
      }
    });
  }
  deleteLicense(data) {
    const payload =  {
        orgId: this.sessionData.orgId,
        rgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        licenseInfoId: data.licenseInfoId
      };
      this.statutoryApiService.deleteOrgLicense(payload).subscribe(response => {
        if (response.responseCode === 'E200') {
          this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
          this.fetchMous();
        } else if (response.responseCode === 'E400') {
          this.snackbar.open(response.responseMessage, 'undo', {duration: 1500});
        }
      });
  }
  openAddressDialog(data) {
    this.dialog.open(ViewFullAddressComponent, {
      width: '30%',
      height: '15rem',
      data: {
        zipCode: data.zipcode,
        country: data.countryNm,
        state: data.stateNm,
        city: data.cityNm,
        contactNo: data.contactPersonNm,
        website: data.website,
        addressLine1: data.addressLine1,
        emailId: data.emailId,
        mobileNo:  data.mobileNo,
        websiteAddress: data.websiteAddress
      }
    });
  }
  openUserDialog(data) {
    this.dialog.open(UsersDetailsComponent, {
      width: '50%',
      height: '30rem',
      data: {
       userDetails: data.responsibleUsersList
      }
    });
  }
  sendEmail(url) {
    const emailUrl = 'https://mail.google.com/mail/u/0/?view=cm&fs=1&to='  + url ;
    window.open(emailUrl);
  }
}
